import React, { useState } from 'react';
import './QuoteDisplay.css';
import { Button, ProgressBar, OverlayTrigger, Tooltip} from 'react-bootstrap';
import Table from 'react-bootstrap/Table';

function QuoteDisplay (props) {

   const [dateShow, setDateShow] = useState(false);
   const [rangeShow, setRangeShow] = useState(false);
   const [diffShow, setDiffShow] = useState(false);
   
   return (
      <Table striped bordered hover responsive="sm" size="sm">
         <DisplayHeader
            showDate={dateShow}
            showRange={rangeShow}
            showDiff={diffShow}
            handleToggleShowDate={(status) => setDateShow(!status)}
            handleToggleShowRange={(status) => setRangeShow(!status)}
            handleToggleShowDiff={(status) => setDiffShow(!status)}
            handleSubmit={props.handleSubmit}
         />
         <DisplayBody
            rows={props.data}
            showDate={dateShow}
            showRange={rangeShow}
            showDiff={diffShow}
            handleSubmit={props.handleSubmit}
         />
      </Table>
   );
}

function DisplayHeader(props) {
   
   return (
      <thead>
         <tr className="text-right">
            <th className="text-left" style={{ width: '25%' }}>
               <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleSubmit(null, null, false)}><span role="img" aria-label="Refresh">&#x1F504;</span></Button>
            </th>
            <th style={{ width: '25%' }}>
               {props.showDate ? <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowDate(props.showDate)}><span role="img" aria-label="Date">&#x1F4C6;</span></Button> : <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowDate(props.showDate)}><span role="img" aria-label="Time">&#x23F2;</span></Button>}
            </th>
            <th style={{ width: '25%' }}>
               {props.showRange ? <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowRange(props.showRange)}><span role="img" aria-label="Range">&#x1F4CA;</span></Button> : <Button style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => props.handleToggleShowRange(props.showRange)}><span role="img" aria-label="Price">&#x1F4B0;</span></Button>}
            </th>
            <th style={{ width: '25%' }}>{props.showDiff ? <Button className="shadow-none" style={{ border: 'none', background: 'none', color: 'black', padding: 0 }} onClick={() => props.handleToggleShowDiff(props.showDiff)}><span>&#x00B1;</span></Button> : <Button className="shadow-none" style={{ border: 'none', background: 'none', color: 'black', padding: 0 }} onClick={() => props.handleToggleShowDiff(props.showDiff)}><span>%</span></Button>}</th>
         </tr>
      </thead>
   )
}

function DisplayBody (props) {

   const refreshQuote = (exchange, symbol) => {
      props.handleSubmit(symbol.toLowerCase(), exchange.toLowerCase(), false, false);
   }

   const refreshChart = (exchange, symbol) => {
      props.handleSubmit(symbol.toLowerCase(), exchange.toLowerCase(), false, true);
   }


   const rows = props.rows.map((row, index) => {
      // independent of market state
      const dateOptions = { month: 'numeric', day: 'numeric' };
      const timeOptions = { hour12: false };
      const dayLow = row.regularMarketDayLow.fmt;
      const dayHigh = row.regularMarketDayHigh.fmt;
      const regularPrice = row.regularMarketPrice.fmt;
      const colonIndex = row.symbol.indexOf('.');
      const symbolString = colonIndex > -1 ? row.symbol.slice(0, colonIndex) : row.symbol;
      const currency = row.currency.toUpperCase() === 'CAD' ? "C$" : "$";
      const symColor = colonIndex > -1 ?
         (row.symbol.slice(colonIndex + 1).toLowerCase() === 'to' ? "primary" :
            (row.symbol.slice(colonIndex + 1).toLowerCase() === 'cn' ? "danger" :
               (row.symbol.slice(colonIndex + 1).toLowerCase() === 'v' ? "dark" :
                  "warning"
               )
            )
         ) : "success";
      const exchange = colonIndex > -1 ?
         (row.symbol.slice(colonIndex + 1).toLowerCase() === 'to' ? "tsx" :
            (row.symbol.slice(colonIndex + 1).toLowerCase() === 'cn' ? "cse" :
               (row.symbol.slice(colonIndex + 1).toLowerCase() === 'v' ? "tsxv" :
                  "neo"
               )
            )
         ) : "us";

      // market state dependent
      let marketTime = row.regularMarketTime.raw;
      let changePercent = row.regularMarketChangePercent.fmt;
      let formattedChange = row.regularMarketChange.fmt;
      let change = row.regularMarketChange.raw;
      let price = row.regularMarketPrice.fmt;
      if ((row.marketState === 'POST' || row.marketState === 'POSTPOST' || row.marketState === 'PREPRE') && row.postMarketTime) {
         marketTime = row.postMarketTime.raw;
         changePercent = row.postMarketChangePercent.fmt;
         formattedChange = row.postMarketChange.fmt;
         change = row.postMarketChange.raw;
         price = row.postMarketPrice.fmt;
      } else if (row.marketState === "PRE" && row.preMarketTime) {
         marketTime = row.preMarketTime.raw;
         changePercent = row.preMarketChangePercent.fmt;
         formattedChange = row.preMarketChange.fmt;
         change = row.preMarketChange.raw;
         price = row.preMarketPrice.fmt;
      }

      const dateStr = new Date(marketTime * 1000).toLocaleDateString('en-CA', dateOptions);
      const timeStr = new Date(marketTime * 1000).toLocaleTimeString('en-CA', timeOptions);
      const textColor = change > 0 ? "text-success" : (change < 0 ? "text-danger" : "text-dark");

      return (

         <tr key={exchange + "-" + symbolString} className="text-right">
            <td className="text-left">
               <Button className={"text-" + symColor + " font-weight-bold"} style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => refreshChart(exchange, symbolString)} ><span role="img" aria-label="Chart">&#x1F4C8;</span></Button>
               {' '}<Button className={"text-" + symColor + " font-weight-bold"} style={{ boxShadow: 'none', border: 'none', background: 'none', padding: 0 }} onClick={() => refreshQuote(exchange, symbolString)} >{symbolString}</Button></td>
            <td>
               {props.showDate ? <span>{dateStr}</span> : <span>{timeStr}</span>}
            </td>
            <td>
               {props.showRange
                  ? <OverlayTrigger overlay={<Tooltip id={exchange + "-" + symbolString}>{dayLow +"-"+dayHigh}</Tooltip>}>
                     <ProgressBar key={1} variant={symColor} min={dayLow} now={regularPrice} max={dayHigh} label={regularPrice}/>
                  </OverlayTrigger>
                  : <span>{currency}{price}</span>}
            </td>
            <td className={textColor}>
               {props.showDiff ? <span>{formattedChange}</span> : <span>{changePercent}</span>}
            </td>
         </tr>
      )
   });

   return <tbody>{rows}</tbody>;
}

export default QuoteDisplay;
